.systemView {
  margin-bottom: 80px;
  @include mq(m) {
    margin-bottom: 60px;
  }
  &_title {
    text-align: center;
    font-size: 39px;
    margin-bottom: 60px;
    @include mq(m) {
      font-size: 30px;
      margin-bottom: 40px;
    }
  }
}
.systemContents {
  &_section {
    border-bottom: 1px solid black;
    padding-bottom: 60px;
    margin-bottom: 60px;
    &:first-child {
      border-top: 1px solid black;
      padding-top: 70px;
    }
    &-title {
      font-size: 26px;
      margin-bottom: 40px;
    }
    &-text {
      margin-bottom: 50px;
      font-size: 16px;
      &__small {
        margin-bottom: 0;
      }
    }
  }
  &_list {
    &-item {
      width:50%;
      padding: 0 4% 0 0;
      @include mq(m) {
        width: 100%;
        padding: 0;
      }
      &:nth-child(2n) {
        padding: 0;
      }
      &-title {
        width: 155px;
        padding: 3px;
        border: 1px solid black;
        text-align: center;
        margin-bottom: 25px;
      }
      dl {
        margin-bottom: 25px;
        dt {
          font-size: 18px;
          margin-bottom: 5px;
        }
      }
      &-button {
        width: 100%;
        &-images {
          width: 230px;
          @include mq(m) {
            width: calc(100% - 185px);
          }
          img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            object-position: center;
          }
        }
        &-view {
          width: calc(100% - 230px);
          background: #000000;
          padding: 60px 50px;
          font-size: 20px;
          align-items: center;
          @include mq(m) {
            font-size: 14px;
            padding: 30px;
            width: 185px;
          }
        }
      }
    }
  }
}