@use "sass:math";

.workFlow {
  overflow: hidden;
  font-size: 314px;
  color: #F0F0F0;
  white-space: nowrap;
  margin-bottom: 100px;
  @include mq(m){
    font-size: 81px;
    margin-bottom: 60px;
  }
  span {
    display: inline-block;
  }
}
.workMember {
  &_list {
    @include mq(m){
      display: block;
    }
    &-item {
      width: 22.75%;
      margin: 0 3% 90px 0;
      display: flex;
      flex-direction: column;
      @include mq(m){
        width: 225px;
        margin: 0 auto 75px auto;
      }
      &:nth-child(4n) {
        margin: 0 0 90px 0;
        @include mq(m){
          margin: 0 auto 75px auto;
        }
      }
      &-view {
        display: flex;
        margin-top: auto;
        flex-direction: column;
      }
      &-images {
        margin-bottom: 20px;
      }
      a {
        height: 100%;
        display: flex;
        flex-direction: column;
        &:hover {
          .workMember_list-item-title {
            span {
              @include linePostHover;
            }
          }
        }
      }
      &-title {
        font-size: 17px;
        margin-bottom: 25px;
        span {
          display: inline;
          @include linePost;
        }
      }
      &-number {
        font-size: 15px;
        margin-bottom: 15px;
      }
    }
  }
}