.p_headline {
  flex-direction: column;
  background: var(--main-color);
  margin-top: 120px;
  padding: 70px 10.27% 70px 10.278%;
  @include mq(m) {
    padding: 35px 9.6% 35px 9.6%;
    margin-top: 84px;
  }
  &-en {
    display: block;
    font-size: 56px;
    margin-bottom: 20px;
    @include mq(m) {
      font-size: 40px;
      margin-bottom: 20px;
    }
  }
  &-jp {
    display:block;
    color: white;
    font-size: 22px;
    @include mq(m) {
      font-size: 20px;
    }
  }
  &-text {
    font-size: 16px;
    margin-top: 30px;
    @include mq(m) {
      font-size: 14px;
    }
    &__large {
      font-size: 18px;
      @include mq(m) {
        font-size: 14px;
      }
    }
  }
  &__visual {
    width: 48.61%;
    margin-top: 410px;
    min-width: 660px;
    margin-bottom: 200px;
    @include mq(m) {
      min-width: inherit;
      width: 91.73%;
      margin-top: 365px;
      margin-bottom: 70px;
    }
    &-jp {
      font-size: 75px;
      color: black;
      @include mq(m) {
        font-size: 44px;
      }
    }
  }
}