.p_flow {
  &-list {
    padding: 0 0 110px 0;
    margin-bottom: 110px;
    border-bottom: 1px solid black;
    @include mq(m) {
      padding: 0 0 70px 0;
      margin-bottom: 70px;
    }
    &:last-child {
      margin-bottom: 0;
      border-bottom: none;
    }
  }
  &-title {
    font-size: 39px;
    margin-bottom: 100px;
    @include mq(m) {
      font-size: 30px;
      margin-bottom: 40px;
    }
  }
  &-step {
    &-item {
      span {
        display: block;
        width: 48.25%;
        padding: 20px;
        background: black;
        text-align: center;
        font-size: 25px;
        color: white;
        margin: 0 auto;
        @include mq(m) {
          width: 100%;
          font-size: 18px;
        }
      }
      &:after {
        background: black;
        display: block;
        width: 1px;
        height: 28px;
        margin: 20px auto;
        content: "";
      }
      &:last-child {
        &:after {
          display: none;
        }
      }
    }
  }
  &-text {
    font-size: 18px;
  }
}