.crossTalkMember {
  margin-bottom: 170px;
  @include mq(m) {
    margin-bottom: 60px;
  }
  &_list {
    &-item {
      width: 47.64%;
      margin: 0 4.72% 80px 0;
      position: relative;
      z-index: 2;
      @include mq(m) {
        width: 100%;
        margin: 0 0 50px 0;
      }
      &:nth-child(2n) {
        margin: 0 0 80px 0;
        @include mq(m) {
          margin: 0 0 50px 0;
        }
      }
      &-layout {
        margin-bottom: 25px;
        &-images {
          width: 42.07%;
        }
        &-view {
          width: calc(100% - 42.07%);
          padding: 20px 0 0 15px;
          @include mq(m) {
            padding: 0 0 0 10px;
          }
          &-title {
            font-size: 18px;
            margin-bottom: 20px;
            @include mq(m) {

            }
          }
          &-text {
            font-size: 14px;
          }
        }
      }
      &-read {
        font-size: 14px;
      }
    }
  }
  &_wrapper {
    position: relative;
    &-images {
      z-index: 1;
      width: 214px;
      @include mq(m) {
        display: none;
      }
    }
  }
}