.p_visual {
  position: relative;
  margin-top: 120px;
  margin-bottom: 270px;
  z-index: 2;
  @include mq(m) {
    margin-top: 83px;
    margin-bottom: 70px;
  }
  &__irregular {
    margin-bottom:370px;
    @include mq(m) {
      margin-bottom:80px;
    }
  }
  &-images {
    position: relative;
    z-index: 2;
  }
  &-view {
    position: absolute;
    left: 0;
    top: auto;
    z-index: 2;
    transform: translateY(-50%) translateX(0);
    width: 58.88%;
    padding: 70px 10.27% 70px 10.278%;
    margin-top: 0;
    @include mq(m) {
      position: relative;
      left: auto;
      transform: translateY(0) translateX(0);
      width: 100%;
      padding: 50px 9.6%;
    }
  }
}