.p_table {
  table {
    width: 100%;
    font-size: 16px;
    border-bottom: 1px solid #D5D5D5;
    @include mq(m) {
      font-size: 14px;
      display: block;
    }
    tr {
      border-top: 1px solid #D5D5D5;
      @include mq(m) {
        display: block;
      }
    }
    th {
      width: 25.87%;
      text-align: left;
      padding: 30px 0;
      @include mq(m) {
        display: block;
        width: 100%;
        padding-bottom: 0;
        margin-bottom: 5px;
      }
    }
    td {
      width: calc(10% - 25.87%);
      padding: 30px 0;
      @include mq(m) {
        display: block;
        width: 100%;
        padding-top: 0;
      }
    }
  }
}