.solutionSlider {
  overflow: hidden;
  &_container {
    position: relative;
  }
  &_wrapper {
    @include mq(m) {
      height: auto !important;
      display: block !important;
    }
  }
  &_arrow {
    @include mq(m) {
      display: none;
    }
    &-button {
      width: 50px;
      height: 50px;
      position: absolute;
      top: 50%;
      z-index: 4;
      background: white;
      transform: translateY(-50%) translateX(0);
      opacity: 1;
      transition: 0.5s all var(--transition);
      &.swiper-button-disabled {
        opacity: 0.5;
        pointer-events: none;
      }
      &__prev {
        left: 0;
      }
      &__next {
        right: 0;
      }
    }
  }
  &_list {
    @include mq(m) {
      height: auto !important;
    }
    &-images {
      width: 41.11%;
      @include mq(m) {
        width: 100%;
      }
      img {
        @include objectfit;
      }
    }
    &-view {
      width: calc(100% - 41.11%);
      background: black;
      padding: 90px 10.27%;
      @include mq(m) {
        width: 100%;
        padding: 40px 9.6%;
      }
      &-number {
        font-size: 16px;
        margin-bottom: 20px;
      }
      &-title {
        font-size: 30px;
        margin-bottom: 20px;
      }
      &-text {
        font-size: 16px;
        margin-bottom: 40px;
      }
    }
  }
  .p_button {
    @include mq(m) {
      width: 100%;
    }
  }
  &_thumb {
    overflow: hidden;
    @include mq(m) {
      display: none;
    }
    &-item {
      width: 14.28%;
      position: relative;
      cursor: pointer;
      &.swiper-slide-thumb-active {
        .solutionSlider_thumb-item-images {
          &:after {
            background: var(--main-color);
          }
        }
      }
      &-images {
        position: relative;
        z-index: 1;
        &:after {
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: black;
          opacity: 0.4;
          z-index: 1;
          position: absolute;
          content: "";
        }
      }
      &-view {
        width: 100%;
        z-index: 4;
        text-align: center;
        .en {
          display: block;
          font-size: 14px;
          margin-bottom: 10px;
        }
        .jp {
          display: block;
          font-size: 16px;
        }
      }
    }
  }
}
