body.isHome {
  //overflow: hidden;
  & > *:not(.p_loading) {
    opacity: 0;
    transition: 0.5s all var(--transition);
  }

  &[data-load-state="complete"] {
    //overflow: auto;
    > *:not(.p_loading) {
      opacity: 1;
    }
  }
}
.p_loading {
  pointer-events: none;
  position: fixed;
  opacity: 1;
  width: 100%;
  height: 100%;
  z-index: 9999;
  top: 0;
  left: 0;
  opacity: 1;
  transition: 0.5s all var(--transition);
  @include mq(m) {
    background: white;
  }
  video {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    @include mq(m) {
      height: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translateY(-50%) translateX(-50%);
    }
  }
}
