.p_flex {
  margin-bottom: 130px;
  @include mq(m) {
    margin-bottom: 60px;
    flex-direction: column-reverse;
  }
  &-body {
    margin-bottom: 60px;
    @include mq(m) {
      flex-direction: column-reverse;
      margin-bottom: 30px;
    }
  }
  &__mobile {
    @include mq(m) {
      flex-direction: column;
    }
  }
  &-view {
    width: 47.84%;
    margin-right: auto;
    @include mq(m) {
      width: 100%;
    }
    &-title {
      font-size: 30px;
      margin-bottom: 50px;
      @include mq(m) {
        font-size: 20px;
        margin-bottom: 30px;
      }
    }
    &-text {
      font-size: 16px;
      @include mq(m) {
        font-size: 14px;
      }
    }
  }
  &-images {
    width: 45.25%;
    margin-left: auto;
    @include mq(m) {
      width: 100%;
      margin-bottom: 40px;
    }
    figure {
      img {
        display: block;
        margin-bottom: 35px;
      }
      figcaption {
        font-size: 12px;
        &.large {
          font-size: 18px;
        }
      }
    }
  }
  &-caption {
    font-size: 12px;

  }
  &__reverse {
    flex-direction: row-reverse;
    @include mq(m) {
      flex-direction: column-reverse;
    }
    .p_flex-view {
      margin-left: auto;
    }
    .p_flex-images {
      margin-right: auto;
    }
  }
}