.p_staff {
  margin-top: 250px;
  margin-bottom: 160px;
  overflow: hidden;
  @include mq(m) {
    display: block;
    margin-top: 83px;
    margin-bottom: 70px;
  }
  &-images {
    width: 48.61%;
    height: 500px;
    @include mq(m) {
      height: 100%;
      width: 100%;
      margin-bottom: 60px;
    }
    img {
      @include objectfit;
    }
  }
  &-view {
    width: calc(100% - 48.61%);
    padding:60px 10.27% 0 40px;
    @include mq(m) {
      width: 100%;
      padding: 0 9.6%;
      margin-bottom: 30px;
    }
    &-title {
      font-size: 35px;
      margin-bottom: 70px;
      white-space: nowrap;
      @include mq(m) {
        font-size: 20px;
        margin-bottom: 50px;
      }
    }
    &-number {
      font-size: 22px;
      margin-bottom: 15px;
      @include mq(m) {
        font-size: 18px;
        margin-bottom: 10px;
      }
    }
    &-name {
      font-size: 22px;
      margin-bottom: 15px;
      @include mq(m) {
        font-size: 18px;
        margin-bottom: 10px;
      }
    }
    &-text {
      font-size: 16px;
      @include mq(m) {
        font-size: 14px;
      }
    }
  }
  &-headline {
    margin-left: auto;
    margin-top: -90px;
    width: 79%;
    flex-direction: inherit;
    align-items: center;
    position: relative;
    z-index: 4;
    padding-top: 70px;
    padding-bottom: 70px;
    @include mq(m) {
      width: 100%;
      margin: 0;
    }
    &-main {
      width: 34.18%;
      @include mq(m) {
        width: 100%;
      }
      &-en {
        font-size: 70px;
      }
    }
    &-text {
      width: calc(100% - 34.18%);
      margin-top: 0;
      @include mq(m) {
        width: 100%;
        margin-top: 30px;
      }
    }
  }
}