.p_training {
  &-mv {
    margin-bottom: 120px;
    position: relative;
    @include mq(m) {
      margin-bottom: 70px;
    }
    &-images {
      position: relative;
      z-index: 1;
    }
    &-view {
      width: 330px;
      position: absolute;
      z-index: 2;
      left: 10.27%;
      bottom: -20px;
      text-align: center;
      @include mq(m) {
        width: 150px;
        left: 9.6%;
      }
      span {
        display: block;
        font-size: 172px;
        margin-bottom: 5px;
        @include mq(m) {
          font-size: 77px;
        }
      }
      p {
        background: black;
        color: white;
        font-size: 18px;
        padding: 15px;
        @include mq(m) {
          font-size: 14px;
          padding: 10px;
        }
      }
    }
  }
  &-article {
    margin-bottom: 60px;
    @include mq(m) {
      margin-bottom: 30px;
    }
    &-title {
      font-size: 35px;
      margin-bottom: 40px;
      @include mq(m) {
        font-size: 28px;
        margin-bottom: 30px;
        line-height: 1.6;
      }
    }
    &-view {
      &-name {
        font-size: 19px;
        margin-bottom: 5px;
      }
      &-job {
        font-size: 16px;
        @include mq(m) {
          font-size: 14px;
        }
      }
    }

  }
  &-section {
    margin-bottom: 70px;
    @include mq(m) {
      margin-bottom: 50px;
    }
    &-title {
      font-size: 24px;
      margin-bottom: 35px;
      @include mq(m) {
        font-size: 18px;
        margin-bottom: 30px;
      }
      span {
        @include linePost;
      }
    }
    &-text {
      font-size: 16px;
      @include mq(m) {
        font-size: 14px;
      }
    }
  }
  &-profile {
    background: black;
    padding: 100px 0;
    @include mq(m) {
      padding: 70px 0;
    }
    &-layout {
      margin-bottom: 70px;
      @include mq(m) {
        margin-bottom: 30px;
      }
      &-images {
        width: 35.21%;
        @include mq(m) {
          width: 100%;
          margin-bottom: 30px;
        }
      }
      &-view {
        width: calc(100% - 35.21%);
        padding: 0 0 0 3%;
        @include mq(m) {
          width: 100%;
          padding: 0;
        }
        &-title {
          font-size: 24px;
          margin-bottom: 30px;
          @include mq(m) {
            margin-bottom: 25px;
          }
        }
        &-text {
          font-size: 16px;
          @include mq(m) {
            font-size: 14px;
          }
        }
      }
    }
    &-definition {
      dl {
        align-items: center;
        @include mq(m) {
          display: block;
        }
        dt {
          text-align: center;
          border: 1px solid white;
          padding: 10px 20px;
          width: 220px;
          @include mq(m) {
            font-size: 15px;
            margin-bottom: 20px;
          }
        }
        dd {
          width: calc(100% - 220px);
          padding: 0 0 0 40px;
          font-size: 18px;
          @include mq(m) {
            width: 100%;
            padding: 0;
            line-height: 1.8;
          }
        }
      }
    }
  }
}