.p_view {
  color: white;
  display: block;
  @include mq(m){
    width: max-content;
  }
  &__flex {
    .p_view-jp {
      padding: 0;
      display: flex;
      &-in {
        align-items: center;
        display: flex;
        padding: 0 0 5px 0;
        span {
          background-image: none;
        }
        i {
          display: block;
          font-size: 14px;
          margin: 0 0 0 20px;
        }
      }
    }
  }
  &:hover {
    .p_view-en {
      span {
        @include linePostHover;
      }
    }
    .p_view-jp {
      span {
        @include linePostHover;
      }
    }
  }
  &-en {
    display: block;
    font-size: 12px;
    margin-bottom: 15px;
    padding: 0 0 5px 0;
    @include mq(m){
      font-size: 10px;
      margin-bottom: 10px;
    }
    span {
      display: inline;
      @include linePostWhite;
    }
  }
  &-jp {
    display: block;
    font-size: 18px;
    padding: 0 0 5px 0;
    @include mq(m){
      font-size: 15px;
    }
    span {
      display: inline;
      @include linePostWhite;
    }
    &__large {
      font-size: 26px;
      margin-top: 20px;
      padding: 0 0 10px 0;
      @include mq(m){
        margin-top: 10px;
      }
      span {
        @include mq(m){
          font-size: 18px;
          line-height: 1.8;
        }
      }
    }
  }
}