.internship {
  &_p_ttl {
    &__white {
      color: white;
    }
    &-en {
      font-size: 35px;
      margin-bottom: 20px;
      letter-spacing: 0.1em;
    }
    &-ja {
      font-size: 20px;
      font-weight: 700;
      @include mq(m) {
        font-size: 16px;
      }
    }
  }

  &_header {
    top: 0;
    left: 0;
    width: 100%;
    position: fixed;
    z-index: 99;
    min-width: 1140px;
    @include mq(m) {
      min-width: inherit;
    }
    &-body {
      align-items: center;
      padding: 0 40px 0 25px;
      background: white;
      @include mq(m) {
        padding: 0 30px 0 30px;
      }
    }
    &-logo {
      margin-right: auto;
      align-items: center;
      &-link {
        display: block;
        width: 128px;
        margin: 0 20px 0 0;
        @include mq(m) {
          width: 90px;
        }
      }
      &-name {
        display: block;
        font-size: 12px;
        @include mq(m) {
          display: none;
        }
      }
    }
    &-navigation {
      margin-left: auto;
    }
  }

  &_navigation {
    &-list {
      &-item {
        margin: 0 0 0 25px;
        &-link {
          padding: 38px 0;
          display: block;
          @include mq(m) {
            padding: 28px 0;
          }

          &:hover {
            span {
              &:before {
                animation: anim 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
                @keyframes anim {
                  0% {
                    left: -100%;
                  }
                  100% {
                    left: 0;
                  }
                }
              }
            }
          }
          a {
            display: block;
            position: relative;
            overflow: hidden;
            transition: all 0.5s var(--transition);
            @include mq(m) {
              font-size: 15px;
            }
            &:before {
              z-index: 2;
              content: "";
              width: 100%;
              height: 1px;
              background: black;
              position: absolute;
              bottom: 0;
              left: -100%;
              animation: anim-out 0.6s cubic-bezier(0.165, 0.84, 0.44, 1)
                forwards;
              @keyframes anim-out {
                0% {
                  left: 0;
                }
                100% {
                  left: 100%;
                }
              }
            }
          }
        }
      }
    }
  }

  &_mv {
    width: 100%;
    height: 100%;
    background: white;
    overflow: hidden;
    position: relative;
    &-box {
      width: 750px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      white-space: nowrap;
      text-align: center;
      @include mq(m) {
        width: 327px;
      }
      &-img {
        margin-bottom: 50px;
        @include mq(m) {
          margin-bottom: 25px;
        }
      }
      &-txt {
        font-size: 120px;
        @include mq(m) {
          font-size: 52px;
        }
      }
    }
  }

  &_work {
    background: #dc000c;
    padding: 230px 0 220px;
    position: relative;
    overflow: hidden;

    @include mq(m) {
      padding: 120px 0 60px;
    }

    &-box {
      position: relative;
      z-index: 2;
      color: white;
      &-head {
        font-size: 43px;
        font-weight: 700;
        margin-bottom: 120px;
        text-align: center;

        @include mq(m) {
          font-size: 30px;
          margin-bottom: 110px;
          line-height: 1.6;
        }
      }
      &-ttl {
        text-align: center;
        margin-bottom: 70px;

        @include mq(m) {
          margin-bottom: 55px;
        }
      }
      &-points {
        width: 850px;
        margin: 0 auto;

        @include mq(m) {
          width: 79.46%;
        }

        li {
          margin-bottom: 50px;
          &:last-child {
            margin-bottom: 0;
          }
          flex-wrap: nowrap;
        }

        &-number {
          flex: 65px 0 0;
          font-size: 35px;
          padding-top: 3px;

          @include mq(m) {
            flex: 40px 0 0;
            font-size: 35px;
            padding-top: 0;
          }
        }
        &-txt {
          font-weight: 700;
          font-size: 20px;
          line-height: 1.8;

          @include mq(m) {
            font-size: 16px;
            line-height: 2;
          }
        }
      }
    }
    &-bg {
      width: 93%;
      max-width: 1165px;
      position: absolute;
      z-index: 1;
      top: 55%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }

  &_movie {
    padding: 205px 0;
    background: #3e3e3e;
    @include mq(m) {
      padding: 70px 0 80px;
    }

    &-box {
      justify-content: space-between;
      @include mq(m) {
        display: block;
      }

      &--left {
        flex: 35.1% 1 1;
        color: white;
        padding-right: 3.4%;
        @include mq(m) {
          padding-right: 0;
          margin-bottom: 40px;
        }
      }
      &--right {
        flex: 61.1% 1 1;
        &-inner {
          padding-top: 56.25%;
          width: 100%;
          position: relative;
          iframe {
            position: absolute;
            z-index: 2;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
          }
        }
      }
      &-ttl {
        margin-bottom: 50px;
      }
      &-txt {
      }
    }
  }

  &_outline {
    padding: 200px 0 185px;
    overflow: hidden;
    @include mq(m) {
      padding: 60px 0 60px;
    }

    &-ttl {
      margin-bottom: 50px;
    }
    &-txt {
      margin-bottom: 85px;

      @include mq(m) {
        line-height: 2;
        margin-bottom: 40px;
      }
    }

    &-table {
      width: 100%;
      margin-bottom: 90px;
      @include mq(m) {
        margin-bottom: 40px;
      }

      tr {
        border-top: 1px solid #d5d5d5;
        &:last-child {
          border-bottom: 1px solid #d5d5d5;
        }

        @include mq(m) {
          display: block;
          padding: 30px 0;
        }
        th,
        td {
          padding: 30px 0;
          @include mq(m) {
            display: block;
            padding: 0;
          }
        }
        th {
          width: 280px;
          padding-right: 20px;
          text-align: left;
          @include mq(m) {
            margin-bottom: 20px;
            padding-right: 0;
            width: auto;
          }
        }
        td {
          small {
            font-size: 12px;
            display: inline-block;
            @include mq(m) {
              display: block;
              line-height: 1.2;
            }
          }
        }
      }
      &-note {
        margin-left: 20px;
        @include mq(m) {
          margin-left: 0;
          margin-top: 10px;
        }
      }

      &-list {
        li {
          margin-bottom: 30px;
          &:last-child {
            margin-bottom: 0;
          }
        }
        &-ttl {
          margin-bottom: 10px;
          span {
            display: inline-block;
            vertical-align: middle;
            letter-spacing: 0.1em;
          }
          &-num {
            font-size: 20px;
            margin-right: 20px;
            @include mq(m) {
              font-size: 16px;
              margin-right: 10px;
            }
          }
          &-txt {
            font-size: 30px;
            @include mq(m) {
              font-size: 24px;
            }
          }
        }
        &-txt {
          margin-bottom: 10px;
        }
      }
    }

    &-figure {
      margin-bottom: 90px;
      padding-left: 10.27%;
      padding-right: 10.27%;

      @include mq(m) {
        overflow-x: auto;
        overflow-y: hidden;
        padding-left: 0;
        padding-right: 0;
        margin-bottom: 65px;
      }
      &-box {
        @include mq(m) {
          width: 680px;
          padding-left: 10.27%;
          padding-right: 10.27%;
        }
      }
    }
    &-figureCaption {
      text-align: center;
      span {
        background-image: linear-gradient(#000, #000);
        background-position: left 100%;
        background-size: 100% 1px;
        background-repeat: no-repeat;
        line-height: 2;
        font-size: 20px;
        font-weight: 700;
        @include mq(m) {
          font-size: 18px;
        }
      }
    }
  }

  &_entry {
    background: #000000;
    padding: 100px 0 120px;
    text-align: center;
    @include mq(m) {
      padding: 65px 0 60px;
    }

    &-ttl {
      font-size: 160px;
      color: #2e2e2e;
      margin-bottom: 40px;
      @include mq(m) {
        font-size: 90px;
      }
    }
    &-txt {
      color: white;
      line-height: 2;
      margin-bottom: 60px;
      @include mq(m) {
        margin-bottom: 35px;
      }
    }
    &-btn {
      margin: 0 auto;
      width: 600px;
      @include mq(m) {
        width: 100%;
      }
    }
  }

  &_program {
    padding: 190px 0 130px;
    @include mq(m) {
      padding: 65px 0 50px;
    }
    &-copy {
      margin-bottom: 80px;
      white-space: nowrap;
      overflow: hidden;
      @include mq(m) {
        margin-bottom: 50px;
      }
      span {
        display: inline-block;
        font-size: 176px;
        white-space: nowrap;
        color: #f0f0f0;
        padding-right: 60px;
        @include mq(m) {
          font-size: 45px;
          padding-right: 30px;
        }
      }
    }

    &-ttl {
      margin-bottom: 100px;
      @include mq(m) {
        margin-bottom: 65px;
      }
    }

    &-list {
      li {
        margin-bottom: 140px;
        &:last-child {
          margin-bottom: 0;
        }

        @include mq(m) {
          margin-bottom: 50px;
        }
      }

      &-img {
        position: relative;
        z-index: 1;
        img {
          position: relative;
          z-index: 1;
        }
        &-num {
          position: absolute;
          left: 10.27%;
          top: 50%;
          transform: translate(0, -50%);
          color: white;
          font-size: 160px;
          z-index: 3;

          @include mq(m) {
            font-size: 40px;
          }
        }
        &:after {
          content: "";
          position: absolute;
          z-index: 2;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          background: rgba(0, 0, 0, 0.2);
        }
      }
      &-box {
        position: relative;
        z-index: 2;
        margin: -70px 0 0 auto;
        width: 79.44%;
        background: #3e3e3e;
        padding: 70px 11%;
        color: white;
        @include mq(m) {
          margin: -20px 0 0 auto;
          width: 89.73%;
          padding: 50px 30px;
        }

        &-ttl {
          font-size: 60px;
          font-weight: 300;
          margin-bottom: 40px;
          line-height: 1.4;
          @include mq(m) {
            font-size: 24px;
            margin-bottom: 35px;
          }
          &-sub {
            margin-top: 30px;
            font-size: 30px;
            @include mq(m) {
              font-size: 20px;
              margin-top: 10px;
            }
          }
        }
        &-txt {
          line-height: 2;
        }
      }

      li {
        &:nth-child(even) {
          .internship_program-list-img {
            &-num {
              left: auto;
              right: 10.27%;
            }
          }
          .internship_program-list-box {
            margin: -70px auto 0 0;

            @include mq(m) {
              margin: -20px auto 0 0;
            }
          }
        }
      }
    }
  }

  &_footer {
    background: #000000;
    padding: 25px 0;
    text-align: center;
    color: white;
    @include mq(m) {
      padding: 15px 0;
    }
    p {
      small {
        font-size: 12px;
      }
    }
  }
}
