.p_list {
  @include mq(m){
    flex-direction: column;
  }
  &__flow {
    @include mq(m){
      flex-direction: initial;
      .p_list-item {
        margin: 0 20px 0 0;
      }
    }
  }
  &__block {
    margin-bottom: 55px;
    @include mq(m){
      margin-bottom: 20px;
    }
  }
  &-item {
    margin: 0 40px 0 0;
    @include mq(m){
      margin: 0 0 30px 0;
    }
    &:last-child {
      margin: 0;
    }
  }
}