.p_section {
  margin-bottom: 170px;
  padding-left:10.27%;
  @include mq(m) {
    margin-bottom: 60px;
    padding-left: 0;
  }
  &-layout {
    @include mq(m) {
      flex-direction: column-reverse;
    }
  }
  &-title {
    font-size: 35px;
    margin-bottom: 60px;
    @include mq(m) {
      font-size: 20px;
      margin-bottom: 50px;
    }
  }
  &-view {
    width: 42.72%;
    margin-right: auto;
    font-size: 16px;
    @include mq(m) {
      width: 100%;
      padding-left: 9.6%;
      padding-right: 9.6%;
      font-size: 14px;
    }
    p {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &-text {
      &-name {
        align-items: center;
        &-job {
          font-size: 18px;
          margin: 0 10px 0 0;
          @include mq(m) {
            font-size: 14px;
          }
        }
        &-figure {
          width: 180px;
          @include mq(m) {
            width: 150px;
          }
        }
      }
    }
  }
  &-images {
    width: 42.72%;
    margin-left: auto;
    @include mq(m) {
      width: 100%;
      margin-bottom: 70px;
    }
  }
  &__staff {
    @include mq(m) {
      margin-bottom: 80px;
    }
    .p_section-layout {
      @include mq(m) {
        flex-direction: column;
      }
    }
    .p_section-images {
      margin-bottom: 0;
    }
    .p_section-view {
      @include mq(m) {
        margin-bottom: 70px;
      }
    }
    &.p_section__reverse {
      .p_section-title {
        @include mq(m) {
          padding: 0 9.6%;
        }
      }
      .p_section-layout {
        @include mq(m) {
          flex-direction: column;
        }
      }
      .p_section-images {
        @include mq(m) {
          padding: 0 9.6% 0 0;
          margin-bottom: 0;
        }
      }
    }
    .p_section-title {
      @include mq(m) {
        padding: 0 9.6%;
      }
    }
    .p_section-images {
      margin-top: 90px;
      @include mq(m) {
        margin-top: 0;
        padding: 0 0 0 9.6%;
      }
    }
  }
  &__reverse {
    padding-left:0;
    padding-right:10.27%;
    @include mq(m) {
      padding: 0;
    }
    .p_section-title {
      padding-left:10.27%;
      @include mq(m) {
        padding: 0;
      }
    }
    .p_section-layout {
      flex-direction: row-reverse;
      @include mq(m) {
        flex-direction: column-reverse;
      }
    }
    .p_section-view {
      margin-right: 0;
      margin-left: auto;
    }
    .p_section-images {
      margin-left: 0;
      margin-right: auto;
    }
  }
  &__normal {
    .p_section-title {
      padding-left:0;
      font-size: 30px;
      @include mq(m) {
        font-size: 20px;
      }
    }
  }
  &__straight {
    padding-left: 0;
    .p_section-view {
      width: 100%;
      @include mq(m) {
        padding: 0;
      }
    }
  }
}