.caseHeader{
  position: relative;
  margin-bottom: 130px;
  padding-top: 70px;
  @include mq(m) {
    padding: 25px 0 35px 0;
    margin-bottom: 60px;
  }
  &:after {
    width: 100%;
    height: calc(100% - 70px);
    background: var(--main-color);
    content: "";
    z-index: 1;
    position: absolute;
    left: 0;
    top: 0;
    @include mq(m) {
      height: 100%;
    }
  }
  &_layout {
    position: relative;
    z-index: 2;
    &-images {
      width: 73.72%;
      position: relative;
      z-index: 1;
      @include mq(m) {
        width: 100%;
      }
    }
    &-view {
      position: relative;
      z-index: 2;
      width: calc(100% - 73.72%);
      padding: 150px 0 0 40px;
      @include mq(m) {
        width: 100%;
        padding: 0;
        margin-top: -40px;
      }
      &-title {
        text-align: right;
        font-size: 90px;
        position: absolute;
        right: 0;
        top: 40px;
        width: 200%;
        @include mq(m) {
          position: relative;
          right: auto;
          top: auto;
          width: 100%;
          font-size: 72px;
        }
      }
      &-year {
        font-size: 16px;
        margin-bottom: 20px;
        @include mq(m) {
          font-size: 14px;
          margin-bottom: 20px;
        }
      }
      &-job{
        font-size: 16px;
        margin-bottom: 10px;
        @include mq(m) {
          font-size: 14px;
          margin-bottom: 20px;
        }
      }
      &-name {
        font-size: 19px;
        @include mq(m) {
          font-size: 17px;
        }
      }
    }
  }
}
.casePath {
  margin-bottom: 150px;
  overflow: hidden;
  @include mq(m) {
    margin-bottom: 60px;
  }
  &_wrapper {
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    padding-top: 70px;
    padding-bottom: 70px;
    @include mq(m) {
      padding-top: 60px;
      padding-bottom: 60px;
    }
  }
  &_body {
  }
  &_section {
    width: 50%;
    @include mq(m) {
      width: 100%;
    }
  }
  &_title {
    text-align: center;
    font-size: 31px;
    margin-bottom: 50px;
    @include mq(m) {
      margin-bottom: 30px;
    }
  }
  &_list {
    &-item {
      dl {
        dt {
          color: #D5D5D5;
          width: 115px;
          font-size: 39px;
          @include mq(m) {
            font-size: 30px;
            width: 95px;
          }
          &.small {
            font-size: 13px;
            @include mq(m) {
              font-size: 12px;
            }
          }
        }
        dd {
          width: calc(100% - 115px);
          padding: 0 0 50px 20px;
          border-left: 1px solid #D5D5D5;
          @include mq(m) {
            width: calc(100% - 95px);
          }
          p {
            font-size: 18px;
            margin-bottom: 10px;
            .number {
              display: block;
              font-size: 18px;
              @include mq(m) {
                line-height: 1.4;
              }
            }
          }
          span {
            display: block;
            font-size: 14px;
          }
        }
      }
      &__small {
        dl {
          dd {
            padding: 0 0 20px 20px;
          }
        }
      }
    }
  }
}