.p_private {
  background: var(--main-color);
  &-layout {
    padding-top: 65px;
    padding-bottom: 65px;
    align-items: center;
    @include mq(m) {
    }
  }
  &-view {
    width: 64.68%;
    padding:0 16.43% 0 0;
    @include mq(m) {
      width: 100%;
      padding: 0;
      margin-bottom: 60px;
    }
    &-title {
      font-size: 70px;
      margin-bottom: 50px;
      @include mq(m) {
        font-size: 40px;
        margin-bottom: 15px;
      }
    }
    &-text {
      font-size: 16px;
      @include mq(m) {
        font-size: 14px;
      }
    }
  }
  &-images {
    width: calc(100% - 64.68%);
    @include mq(m) {
      width: 100%;
      padding: 0 45px;
    }
  }
}