.p_schedule {
  margin-bottom: 130px;
  @include mq(m) {
    margin-bottom: 80px;
  }
  &-body {
    padding:120px 10.27%;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    align-items: center;
    @include mq(m) {
      padding: 60px 0;
    }
  }
  &-title {
    width: 52%;
    font-size: 70px;
    line-height: 1.4;
    @include mq(m) {
      width: 100%;
      font-size: 40px;
      margin-bottom: 40px;
    }
  }
  &-view {
    width: 48%;
    font-size: 16px;
    @include mq(m) {
      width: 100%;
      font-size: 14px;
    }
    dl {
      margin-bottom: 15px;
      dt {
        width: 80px;
      }
      dd {
        width: calc(100% - 80px);
      }
    }
  }
}